		return {
			storageData: {},
			avatar: '',
			nickname: '',
			articleCount: 10,
			followCount: 5,
			messageCount: 67,
			integral: 100,
			articles: [
				{
					id: 1,
					title: '第一篇文章'
				},
				{
					id: 2,
					title: '第二篇文章'
				},
				{
